.mfui-month {
  position: relative;
  z-index: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 284px;
  padding: 28px 0;
}
.mfui-month__header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 282px;
  margin-bottom: 20px;
  text-transform: capitalize;
}
.mfui-month__header svg {
  height: 24.5px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  fill: var(--content);
}
.mfui-month__title {
  font-size: 15px;
  line-height: 24px;
  font-weight: 500;
  width: 224px;
  text-align: center;
}
.mfui-month__weekday-labels {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  width: 272px;
  padding-bottom: 13px;
  border-bottom: 1px solid var(--spbSky1);
  -webkit-column-gap: 22px;
     -moz-column-gap: 22px;
          column-gap: 22px;
}
.mfui-month__weekday {
  font-size: 12px;
  line-height: 18px;
  width: 20px;
  height: 16px;
  color: var(--content);
  font-weight: 600;
  line-height: 24px;
  text-align: center;
  text-transform: capitalize;
}
.mfui-month__days {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  width: 284px;
  margin-top: 8px;
  -webkit-column-gap: 0;
     -moz-column-gap: 0;
          column-gap: 0;
  row-gap: 0;
}
svg.mfui-month__arrow_disabled {
  cursor: default;
  opacity: 0.5;
}
