.mfui-logo {
  display: inline-block;
  vertical-align: top;
}
.mfui-logo_view_horizontal,
.mfui-logo_view_vertical {
  width: 129px;
  vertical-align: top;
}
@media screen and (min-width: 1024px) {
  .mfui-logo_view_horizontal,
  .mfui-logo_view_vertical {
    width: 146px;
  }
}
@media screen and (min-width: 1280px) {
  .mfui-logo_view_horizontal,
  .mfui-logo_view_vertical {
    width: 158px;
  }
}
.mfui-logo__svg {
  display: block;
  width: 100%;
}
.mfui-logo__img {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.mfui-logo_view_horizontal .mfui-logo__img {
  min-height: 23px;
}
@media screen and (min-width: 1024px) {
  .mfui-logo_view_horizontal .mfui-logo__img {
    height: 26px;
  }
}
@media screen and (min-width: 1024px) {
  .mfui-logo_view_horizontal .mfui-logo__img {
    min-height: 28px;
  }
}
