.Modal, .Modal__overlay {
   position: fixed;
   top: 0;
   right: 0;
   left: 0;
   bottom: 0;
}

.Modal {

   &__modal {
      position: absolute;
      top: 50%;
      left: 50%;
      padding: 56px 56px 32px;
      transform: translate(-50%, -50%);
      max-width: 872px;
      width: 100%;
      z-index: 100;
      background: #FFFFFF;
      box-shadow: 0 0 16px rgba(0, 0, 0, 0.2);
      border-radius: 24px;
   }

   &__close_btn {
      height: 40px;
      width: 40px;
      position: absolute;
      display: flex;
      top: 16px;
      right: 16px;
      mask: url("../../assets/icons/close_modal.svg") center no-repeat;
      background: #000;
   }
}

:global {
   .modal-enter {
      opacity: 0;

      .modal-container {
         transform: translate(-50%, -50%) scale(1.1);
      }
   }

   .modal-enter-active {
      opacity: 1;
      transition: .05s ease-in;

      .modal-container {
         transform: translate(-50%, -50%) scale(1);
         transition: .08s ease-in;
      }
   }

   .modal-exit-active {
      opacity: 0;
      transition: .08s ease-in;

      .modal-container {
         transform: translate(-50%, -50%) scale(0.9);
         transition: .05s ease-in;
      }
   }
}

