.mfui-header {
  font-family: inherit;
  font-weight: 400;
}
.mfui-header__addition {
  display: inline-block;
  margin-left: 12px;
}
.mfui-header_level_h1 {
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .mfui-header_level_h1 {
    font-size: 36px;
    line-height: 40px;
  }
}
@media screen and (min-width: 1024px) {
  .mfui-header_level_h1 {
    font-size: 44px;
    line-height: 48px;
    letter-spacing: 0.5px;
  }
}
@media screen and (min-width: 1280px) {
  .mfui-header_level_h1 {
    font-size: 52px;
    line-height: 56px;
  }
}
.mfui-header_margin.mfui-header_level_h1 {
  margin: 0 0 32px 0;
}
.mfui-header_level_h2 {
  font-weight: 600;
  font-size: 22px;
  line-height: 28px;
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .mfui-header_level_h2 {
    font-size: 26px;
    line-height: 32px;
  }
}
@media screen and (min-width: 1024px) {
  .mfui-header_level_h2 {
    font-size: 28px;
    line-height: 36px;
    letter-spacing: 0.5px;
  }
}
@media screen and (min-width: 1280px) {
  .mfui-header_level_h2 {
    font-size: 32px;
    line-height: 40px;
  }
}
.mfui-header_margin.mfui-header_level_h2 {
  margin: 56px 0 24px 0;
}
.mfui-header_level_h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
}
@media screen and (min-width: 1024px) {
  .mfui-header_level_h3 {
    letter-spacing: 0.5px;
  }
}
@media screen and (min-width: 1280px) {
  .mfui-header_level_h3 {
    font-size: 20px;
    line-height: 28px;
  }
}
.mfui-header_margin.mfui-header_level_h3 {
  margin: 32px 0 24px 0;
}
.mfui-header_level_h5 {
  font-size: 15px;
  line-height: 24px;
  font-weight: 500;
}
.mfui-header_space_tight.mfui-header_level_h5 {
  line-height: 18px;
}
.mfui-header_margin.mfui-header_level_h5 {
  margin: 24px 0 16px 0;
}
.mfui-header_color_default {
  color: var(--content);
}
.mfui-header_color_black {
  color: var(--stcBlack);
}
.mfui-header_color_green {
  color: var(--brandGreen);
}
.mfui-header_color_purple {
  color: var(--brandPurple);
}
.mfui-header_color_white {
  color: var(--stcWhite);
}
.mfui-header_color_blue {
  color: var(--systemBlue);
}
.mfui-header_color_inherit {
  color: inherit;
}
.mfui-header_h-align_left {
  text-align: left;
}
.mfui-header_h-align_center {
  text-align: center;
}
.mfui-header_h-align_right {
  text-align: right;
}
