.mfui-day {
  position: relative;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  width: 32px;
  height: 32px;
  padding: 0 10px 8px 0;
  border: none;
  color: var(--content);
  line-height: 24px;
  text-decoration: none;
  background-color: rgba(0, 0, 0, 0);
  outline: none;
  cursor: pointer;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-size: 12px;
  line-height: 18px;
}
.mfui-day:last-child {
  padding-right: 0;
}
.mfui-day:not(:disabled):not(.mfui-day_active):hover:after,
.mfui-day:not(:disabled):not(.mfui-day_active):focus:after,
.mfui-day_active:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 32px;
  height: 32px;
  border-radius: 50% !important;
  color: var(--content);
  background-color: var(--spbSky0);
}
.mfui-day:not(:disabled):not(.mfui-day_active):hover:after {
  color: var(--content);
  background-color: var(--spbSky0);
}
.mfui-day:not(:disabled):not(.mfui-day_active):focus:after,
.mfui-day_active:after {
  background-color: var(--brandGreen);
}
.mfui-day_disabled {
  cursor: default;
  opacity: 0.3;
  pointer-events: none;
}
.mfui-day_active,
.mfui-day:not(:disabled):not(.mfui-day_active):focus {
  color: var(--stcWhite);
}
.mfui-day_active + .mfui-day_active:before,
.mfui-day_between:hover + .mfui-day:not(.mfui-day_monday).mfui-day_active:before {
  content: '';
  position: absolute;
  top: 0;
  left: -22px;
  z-index: -1;
  display: block;
  width: 32px;
  height: 32px;
  background-color: var(--spbSky1);
}
.mfui-day_between {
  position: relative;
  border-radius: 0;
}
.mfui-day_between:before {
  content: '';
  position: absolute;
  top: 0;
  left: -22px;
  z-index: -1;
  display: block;
  height: 32px;
  padding: 0 20px;
  background-color: var(--spbSky1);
}
.mfui-day_between:after {
  content: '';
  position: absolute;
  top: 0;
  right: -16px;
  z-index: -1;
  display: block;
  width: 40px;
  height: 32px;
  background-color: var(--spbSky1);
}
.mfui-day_sunday,
.mfui-day_last {
  border-radius: 50%;
}
.mfui-day_sunday:after,
.mfui-day_last:after {
  right: 10px;
  width: 32px;
  border-radius: 50%;
}
.mfui-day_last:after {
  right: 0;
}
.mfui-day_monday,
.mfui-day_first {
  border-radius: 50%;
}
.mfui-day_monday:before,
.mfui-day_first:before {
  left: 0;
  width: 32px;
  padding: 0;
  border-radius: 50%;
}
.mfui-day_sunday.mfui-day_first {
  border-radius: 50%;
}
.mfui-day__label {
  position: relative;
  z-index: 99;
}
